import React, { useState} from 'react';
import TextInput from "../text-input";
import ErrorSpan from "../error-span";
import style from './style.module.scss'

export interface LabeledTextInputProps {
    entityName: string;
    value: string;
    onChange: (entityName: string, event: string) => string | undefined | void;
    type?: string;
    inputType?: "input" | "textarea";
}

export default function LabeledTextInput(props: LabeledTextInputProps){

    const {entityName, onChange, value, type ,inputType} = props;

    const [showError,setShowError] = useState(false);
    const [errorMessage,setErrorMessage] = useState('');

    function handleChange(value: string){
        setShowError(false);
        const err = onChange(entityName,value);
        if(err){
            setErrorMessage(err);
            setShowError(true);
        }
    }

    return (
        <div className={style.property}>
            <span>{entityName}:</span>
            <TextInput
                inputType={inputType}
                type={type}
                value={value}
                onChange={handleChange}
            />
            <ErrorSpan show={showError} message={errorMessage} />
        </div>
    )
}
