import React from 'react';
import AccordionContainer from "../../util/accordion-container";
import LabeledTextInput from "../../util/labeled-text-input";
import {EventElement} from "../../../types";

export interface EventComponentProps {
    lineElement: EventElement;
    onChange:(element: EventElement) => void;
}

export default function EventComponent(props: EventComponentProps){

    const {lineElement, onChange} = props;

    function handleTextChange(value: string){
        const clone = { ...lineElement };
        clone.event = value;
        onChange(clone);
    }

    return (
        <AccordionContainer cardTitle="Event">
            <LabeledTextInput entityName="eventName" value={lineElement.event} onChange={handleTextChange}/>
        </AccordionContainer>
    )
}
