import * as React from "react";
import arrayMove from "array-move";
import { LineElement, Script } from "../../../types";
import * as typeChecker from "../../../util";
import Dialogue from "../dialogue";
import BackgroundChange from "../background-change";
import ChoiceComponent from "../choice/choice";
import ScriptChange from "../script-change";
import EventComponent from "../event-component";
import LineElementBody from "../line-element-body";

export interface LineElementProps {
  actors: string[];
  backgrounds: string[];
  scripts: Script[];
  lineElements: LineElement[];
  onActorAdd: (actor: string) => void;
  onBackgroundAdd: (background: string) => void;
  onChange: (lineElements: LineElement[]) => void;
}

export function LineElements(props: LineElementProps) {
  const {
    actors,
    backgrounds,
    lineElements,
    scripts,
    onActorAdd,
    onBackgroundAdd,
    onChange,
  } = props;

  function handleChange(lineElement: LineElement) {
    const clone = [...lineElements];
    const index = clone.findIndex((item) => item.id === lineElement.id);

    if (index === -1) {
      throw Error("changed line element couldn't be found!");
    }
    clone[index] = lineElement;
    onChange(clone);
  }

  function lineElementUp(lineElement: LineElement) {
    const itemIndex = lineElements.findIndex(
      (item) => lineElement.id === item.id
    );
    if (itemIndex !== -1 && itemIndex !== 0) {
      onChange(arrayMove(lineElements, itemIndex, itemIndex - 1));
    }
  }

  function lineElementDown(lineElement: LineElement) {
    const itemIndex = lineElements.findIndex(
      (item) => lineElement.id === item.id
    );
    if (itemIndex !== -1 && itemIndex !== lineElements.length - 1) {
      onChange(arrayMove(lineElements, itemIndex, itemIndex + 1));
    }
  }

  function deleteLineElement(lineElement: LineElement) {
    onChange(lineElements.filter((item) => item.id !== lineElement.id));
  }

  return (
    <div>
      <div>
        {lineElements.map((item: LineElement) => {
          if (typeChecker.isDialogueElement(item)) {
            return (
              <LineElementBody
                key={item.id}
                onUp={() => lineElementUp(item)}
                onDown={() => lineElementDown(item)}
                onDelete={() => deleteLineElement(item)}
              >
                <Dialogue
                  lineElement={item}
                  onChange={handleChange}
                  actors={actors}
                  onActorAdd={onActorAdd}
                />
              </LineElementBody>
            );
          }
          if (typeChecker.isBackgroundChangeElement(item)) {
            return (
              <LineElementBody
                key={item.id}
                onUp={() => lineElementUp(item)}
                onDown={() => lineElementDown(item)}
                onDelete={() => deleteLineElement(item)}
              >
                <BackgroundChange
                  backgrounds={backgrounds}
                  actors={actors}
                  lineElement={item}
                  onChange={handleChange}
                  onActorLeftAdd={onActorAdd}
                  onActorRightAdd={onActorAdd}
                  onBackgroundAdd={onBackgroundAdd}
                />
              </LineElementBody>
            );
          }
          if (typeChecker.isChoiceElement(item)) {
            return (
              <LineElementBody
                key={item.id}
                onUp={() => lineElementUp(item)}
                onDown={() => lineElementDown(item)}
                onDelete={() => deleteLineElement(item)}
              >
                <ChoiceComponent
                  key={item.id}
                  lineElement={item}
                  scripts={scripts}
                  actors={actors}
                  onActorAdd={onActorAdd}
                  onChange={handleChange}
                />
              </LineElementBody>
            );
          }
          if (typeChecker.isScriptChangeElement(item)) {
            return (
              <LineElementBody
                key={item.id}
                onUp={() => lineElementUp(item)}
                onDown={() => lineElementDown(item)}
                onDelete={() => deleteLineElement(item)}
              >
                <ScriptChange
                  key={item.id}
                  lineElement={item}
                  scripts={scripts}
                  onChange={handleChange}
                />
              </LineElementBody>
            );
          }
          if (typeChecker.isEventElement(item)) {
            return (
              <LineElementBody
                key={item.id}
                onUp={() => lineElementUp(item)}
                onDown={() => lineElementDown(item)}
                onDelete={() => deleteLineElement(item)}
              >
                <EventComponent
                  key={item.id}
                  lineElement={item}
                  onChange={handleChange}
                />
              </LineElementBody>
            );
          }
          return undefined;
        })}
      </div>
    </div>
  );
}
