import React from "react";
import Button from "../util/button";
import style from "./style.module.scss";

export interface ScriptCreatorProps {
  onCreateElement: (
    elementName:
      | "dialogue"
      | "choice"
      | "background-change"
      | "script-change"
      | "event"
  ) => void;
}

export default function ScriptCreator(props: ScriptCreatorProps) {
  const { onCreateElement } = props;

  return (
    <div className={style.scriptCreator}>
      <Button onClick={() => onCreateElement("dialogue")}>Add Dialogue</Button>
      <Button onClick={() => onCreateElement("choice")}>Add Choice</Button>
      <Button onClick={() => onCreateElement("background-change")}>
        Add Background Change
      </Button>
      <Button onClick={() => onCreateElement("script-change")}>
        Add Script Change
      </Button>
      <Button onClick={() => onCreateElement("event")}>Add Event</Button>
    </div>
  );
}
