import React from "react";
import { ActorOption, DialogueElement } from "../../../types";
import style from './style.module.scss';
import AccordionContainer from "../../util/accordion-container";
import LabeledTextInput from "../../util/labeled-text-input";
import Selector from "../../util/line-elements/selector";

export interface DialogueProps {
  lineElement: DialogueElement;
  onChange: (element: DialogueElement) => void;
  onActorAdd: (value:string) => void;
  actors: string[];
}

export default function Dialogue(props: DialogueProps) {
  const { actors, lineElement, onChange,onActorAdd } = props;

  const actorOptions: ActorOption[] = actors.map((item) => {
    return {
      value: item,
      label: item,
    };
  });

  function handleTextInput(key: string, value: string) {
    const clone = {...lineElement};
    if (key === "title") {
      clone.title = value;
      onChange(clone);
    }
    if (key === "text") {
      clone.text = value;
      onChange(clone);
    }
  }
  function handleActorLeftChange(value: ActorOption | null) {
    const clone = { ...lineElement };
    if (value) {
      clone.actorLeft = value.value;
      onChange(clone);
    }
  }
  function handleActorRightChange(value: ActorOption | null) {
    const clone = { ...lineElement };
    if (value) {
      clone.actorRight = value.value;
      onChange(clone);
    }
  }
  function handleActorAdd(value: string): string | undefined{
    if(value.length === 0){
      return 'Actor Cannot be empty'
    }
    if(actors.find((item)=> item === value)){
      return 'Actor with that name already exists!';
    }
    onActorAdd(value);
    return undefined;
  }
  return (
    <AccordionContainer cardTitle="Dialogue">
      <div>
        <LabeledTextInput
          entityName="title"
          value={lineElement.title}
          onChange={handleTextInput}
        />
        <LabeledTextInput
          inputType="textarea"
          entityName="text"
          value={lineElement.text}
          onChange={handleTextInput}
        />
        <Selector
          entityName="actorLeft"
          addable
          onAddSave={handleActorAdd}
          className={style.selectors}
          value={actorOptions.find(
            (item) => item.value === lineElement.actorLeft
          )}
          options={actorOptions}
          onChange={handleActorLeftChange}
        />
        <Selector
          entityName="actorRight"
          addable
          onAddSave={handleActorAdd}
          className={style.selectors}
          value={actorOptions.find(
            (item) => item.value === lineElement.actorRight
          )}
          options={actorOptions}
          onChange={handleActorRightChange}
        />
      </div>
    </AccordionContainer>
  );
}
