import {
  ChoiceElement,
  LineElement,
  DialogueElement,
  ScriptChangeElement,
  BackgroundChangeElement,
  EventElement,
} from "../types";

export function isDialogueElement(
  element: LineElement
): element is DialogueElement {
  return element.type === "Dialogue";
}

export function isChoiceElement(
  element: LineElement
): element is ChoiceElement {
  return element.type === "Choice";
}
export function isScriptChangeElement(
  element: LineElement
): element is ScriptChangeElement {
  return element.type === "ScriptChangeElement";
}
export function isBackgroundChangeElement(
  element: LineElement
): element is BackgroundChangeElement {
  return element.type === "Background";
}
export function isEventElement(element: LineElement)
    : element is EventElement {
  return element.type === "Event";
}
