import React from "react";
import { Script } from "../../types";
import style from "./style.module.scss";
import AccordionContainer from "../util/accordion-container";
import LabeledTextInput from "../util/labeled-text-input";

export interface ScriptOverviewProps {
  script: Script;
  onChange: (script: Script) => void;
}

export default function ScriptOverview(props: ScriptOverviewProps) {
  const { script, onChange } = props;

  function handleChange(key: string, value: string) {
    const scriptClone = { ...script };
    if (key === "name" || key === "description") {
      scriptClone[key] = value;
      onChange(scriptClone);
    } else throw Error("Key was not in script object!");
  }

  return (
    <AccordionContainer cardTitle={`Script: ${script.name}`} defaultOpen>
      <div className={style.property}>
        <span>ID:</span>
        <span>{script.id}</span>
      </div>
      <LabeledTextInput
        entityName="name"
        value={script.name}
        onChange={handleChange}
      />
      <LabeledTextInput
        entityName="description"
        value={script.description}
        onChange={handleChange}
      />
    </AccordionContainer>
  );
}
