import Select, { StylesConfig } from "react-select";
import React, { useState } from "react";
import {OptionsType} from "react-select/src/types";
import Button from "../../button";
import CreatePopup from "../create-popup";
import style from './style.module.scss';

export interface SelectorProps<T> {
  addable?: boolean;
  options: OptionsType<T>;
  entityName: string;
  onAddSave?: (value: string) => undefined | string;
  className?: string;
  onChange: (value: T | null) => void;
  value?: T
}

export function handleSelectorSave<T>(
    entityName: string,
    value: T,
    allValues: T[],
    compareFunc?: (value: T, index: number, obj: T[]) => unknown,
    onAddFunc?: (name: T) => void
): string | undefined {
  if (!value) {
    return `${entityName} name must not be empty!`;
  }
  const alreadyExists: boolean = compareFunc ? typeof allValues.find(compareFunc) !== "undefined" : allValues.includes(value);
  if (alreadyExists) {
    return `${entityName} with that name already exists!`;
  }
  if (onAddFunc) {
    onAddFunc(value);
  }
  return undefined;
}

export default function Selector<T>(props: SelectorProps<T>) {
  const { addable, entityName, onChange, onAddSave, options,className,value } =
    props;

  const selectStyle: StylesConfig<T, false> = {
    control: (provided, state) => {
      return {
        ...provided,
        borderColor: state.isFocused ? "#3E92CC !important" : "#D6D6D6",
        background: "#2f2930",
        boxShadow: state.isFocused ? "0 0 0 1px #3E92CC" : "unset",
      };
    },
    menu: (base) => {
      return {
        ...base,
        background: "#2f2930",
      };
    },
    option: (base, state) => {
      return {
        ...base,
        color: "#E0E2DB",
        // eslint-disable-next-line no-nested-ternary
        background: state.isSelected
          ? "#079cff"
          : state.isFocused
          ? "#307fb0"
          : "#2f2930",
      };
    },
    valueContainer: (base) => {
      return {
        ...base,
        color: "#E0E2DB",
      };
    },
    placeholder: (base) => {
      return {
        ...base,
        color: "#E0E2DB",
      };
    },
    input: (base) => {
      return {
        ...base,
        color: "#E0E2DB",
      };
    },
    singleValue: (base) => {
      return {
        ...base,
        color: "#E0E2DB",
      };
    },
  };
  const [showPopup, setShowPopup] = useState(false);
  const [popupValue, setPopupValue] = useState("");
  const [showError, setShowError] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  function openPopup() {
    setShowPopup(true);
  }
  function closePopup() {
    setPopupValue("");
    setShowPopup(false);
  }
  function handlePopupChange(value: string) {
    setPopupValue(value);
  }
  function handleSave() {
      setShowError(false);

      if(onAddSave){
          const message = onAddSave(popupValue);
          if(message) {
              setErrorMessage(message);
              setShowError(true);
              return;
          }
      }
      setShowPopup(false);
      setPopupValue('');
  }

  return (
    <div className={className}>
      <div className={style.selector}>
        <span>{entityName}:</span>
        <Select
          value={value}
          onChange={onChange}
          styles={selectStyle}
          options={options}
        />
        {addable ? (
          <Button onClick={openPopup}>Add {entityName}</Button>
        ) : undefined}
      </div>
      {addable ? (
        <CreatePopup
          show={showPopup}
          inputValue={popupValue}
          onInputChanged={handlePopupChange}
          onSave={handleSave}
          onClosePopup={closePopup}
          entityName={entityName}
          errorMessage={errorMessage}
          showError={showError}
        />
      ) : undefined}
    </div>
  );
}
