import React, { useEffect, useState } from "react";
import { OptionsType } from "react-select/src/types";
import AccordionContainer from "../../util/accordion-container";
import style from "./style.module.scss";
import {
  ActorOption,
  BackgroundChangeElement,
  BackgroundOption,
} from "../../../types";
import Selector, {
  handleSelectorSave,
} from "../../util/line-elements/selector";

export interface BackgroundChangeProps {
  backgrounds: string[];
  actors: string[];
  lineElement: BackgroundChangeElement;
  onChange: (lineElement: BackgroundChangeElement) => void;
  onBackgroundAdd: (name: string) => void;
  onActorLeftAdd: (name: string) => void;
  onActorRightAdd: (name: string) => void;
}

export default function BackgroundChange(props: BackgroundChangeProps) {
  const {
    actors,
    backgrounds,
    lineElement,
    onActorLeftAdd,
    onActorRightAdd,
    onBackgroundAdd,
    onChange,
  } = props;
  const backgroundOptions: OptionsType<BackgroundOption> = backgrounds.map(
    (item) => {
      return {
        label: item,
        value: item,
      };
    }
  );
  const actorLeftOptions: OptionsType<ActorOption> = actors.map((item) => {
    return {
      label: item,
      value: item,
    };
  });
  const actorRightOptions: OptionsType<ActorOption> = actors.map((item) => {
    return {
      label: item,
      value: item,
    };
  });
  const [curLineElement, setCurLineElement] = useState(lineElement);

  useEffect(() => {
    setCurLineElement(lineElement);
  }, [lineElement]);
  function handleBackgroundSave(value: string): string | undefined {
    return handleSelectorSave<string>(
      "Background",
      value,
      backgrounds,
      (item) => item === value,
      onBackgroundAdd
    );
  }

  function handleBackgroundSelectChange(value: BackgroundOption | null) {
    const clone = { ...curLineElement };
    clone.background = value?.value ?? "";
    setCurLineElement(clone);
    if (onChange) {
      onChange(curLineElement);
    }
  }

  function handleActorLeftSave(value: string): string | undefined {
    return handleSelectorSave<string>(
      "Background",
      value,
      backgrounds,
      (item) => item === value,
      onActorLeftAdd
    );
  }

  function handleActorLeftSelectChange(value: BackgroundOption | null) {
    const clone = { ...curLineElement };
    clone.actorLeft = value?.value ?? "";
    setCurLineElement(clone);
    if (onChange) {
      onChange(curLineElement);
    }
  }

  function handleActorRightSave(value: string): string | undefined {
    return handleSelectorSave<string>(
      "Background",
      value,
      backgrounds,
      (item) => item === value,
      onActorRightAdd
    );
  }

  function handleActorRightSelectChange(value: BackgroundOption | null) {
    const clone = { ...curLineElement };
    clone.actorRight = value?.value ?? "";
    setCurLineElement(clone);
    if (onChange) {
      onChange(curLineElement);
    }
  }

  return (
    <AccordionContainer cardTitle="Background Card" className={style.card}>
      <div className={style.backgroundWrapper}>
        <Selector
          addable
          options={backgroundOptions}
          entityName="Background"
          onChange={handleBackgroundSelectChange}
          onAddSave={handleBackgroundSave}
          value={backgroundOptions.find(
            (item) => item.value === lineElement.background
          )}
        />
        <Selector
          addable
          options={actorLeftOptions}
          entityName="Actor Left"
          onChange={handleActorLeftSelectChange}
          onAddSave={handleActorLeftSave}
          value={actorLeftOptions.find(
              (item) => item.value === lineElement.actorLeft
          )}
        />
        <Selector
          addable
          options={actorRightOptions}
          entityName="Actor Right"
          onChange={handleActorRightSelectChange}
          onAddSave={handleActorRightSave}
          value={actorRightOptions.find(
              (item) => item.value === lineElement.actorRight
          )}
        />
      </div>
    </AccordionContainer>
  );
}
