import React from "react";
import { v4 as uuid } from "uuid";
import { Choice, ChoiceElement, Script } from "../../../../types";
import AccordionContainer from "../../../util/accordion-container";
import ChoiceOption from "../choice-option";
import Button from "../../../util/button";
import style from "./style.module.scss";
import LabeledTextInput from "../../../util/labeled-text-input";

export interface ChoiceProps {
  lineElement: ChoiceElement;
  scripts: Script[];
  actors: string[];
  onActorAdd: (value: string) => void;
  onChange: (lineElement: ChoiceElement) => void;
}

const defaultChoice: Choice = {
  affectionActor: "",
  affectionChange: 0,
  id: "",
  jumpToScriptId: "",
  text: "",
};

export default function ChoiceComponent(props: ChoiceProps) {
  const { lineElement, scripts, onActorAdd, actors, onChange } = props;

  function handleChoiceChange(choice: Choice) {
    const clone = { ...lineElement };
    const index = clone.choices.findIndex((item) => item.id === choice.id);
    if (index !== -1) {
      clone.choices[index] = choice;
    } else throw Error("Couldn't find choice change!");

    onChange(clone);
  }
  function handleChoiceAdd() {
    const clone = { ...lineElement };
    const newChoice: Choice = { ...defaultChoice, id: uuid() };
    clone.choices.push(newChoice);
    onChange(clone);
  }
  function handleChoiceRemove(choice: Choice) {
    const clone = { ...lineElement };
    clone.choices = lineElement.choices.filter((item) => item.id !== choice.id);
    onChange(clone);
  }
  function handleTextChange(name: string, value: string){
    if(name === "text"){
      const clone = {...lineElement};
      clone.text = value;
      onChange(clone);
    }
  }
  return (
    <AccordionContainer cardTitle="Choice">
      <div>
        <LabeledTextInput entityName="text" value={lineElement.text} onChange={handleTextChange}/>
      </div>
      <div>
        {lineElement.choices.map((item) => {
          return (
            <ChoiceOption
              key={item.id}
              onChange={handleChoiceChange}
              scripts={scripts}
              choice={item}
              actors={actors}
              onActorAdd={onActorAdd}
              onChoiceDelete={handleChoiceRemove}
            />
          );
        })}
      </div>
      <div className={style.bottomBar}>
        <Button onClick={handleChoiceAdd}>Add Choice</Button>
      </div>
    </AccordionContainer>
  );
}
