import React from "react";
import { OptionsType } from "react-select/src/types";
import AccordionContainer from "../../util/accordion-container";
import Selector from "../../util/line-elements/selector";
import { Script, ScriptChangeElement, ScriptOption } from "../../../types";
import style from './style.module.scss';

export interface ScriptChangeProps {
  lineElement: ScriptChangeElement;
  scripts: Script[];
  onChange: (value: ScriptChangeElement) => void;
}

export default function ScriptChange(props: ScriptChangeProps) {
  const { lineElement, scripts, onChange } = props;

  const scriptOptions: OptionsType<ScriptOption> = scripts.map((item) => {
    return {
      value: item.id,
      label: item.name,
    };
  });

  function handleSelectorChange(value: ScriptOption | null) {
    if (!value) return;
    const clone = { ...lineElement };
    clone.scriptChangeToId = value.value;
    onChange(clone);
  }

  return (
    <AccordionContainer cardTitle="Script Change">
      <div className={style.scriptChange}>
        <Selector
            value={scriptOptions.find(
                (item) => item.value === lineElement.scriptChangeToId
            )}
            options={scriptOptions}
            entityName="Script"
            onChange={handleSelectorChange}
        />
      </div>
    </AccordionContainer>
  );
}
