import React, { ReactNode, useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronUp, faChevronDown } from "@fortawesome/free-solid-svg-icons";
import style from "./style.module.scss";

export interface AccordionContainerProps {
  defaultOpen?: boolean;
  children?: ReactNode;
  cardTitle: string;
  className?: string;
  innerClassName?: string;
}

export default function AccordionContainer(props: AccordionContainerProps) {
  const { defaultOpen, children, cardTitle, className, innerClassName } = props;

  const [open, setOpen] = useState(!!defaultOpen);

  function clickOpen() {
    setOpen(!open);
  }

  return (
    <div
      style={!open ? { borderBottomWidth: "0", overflow: "hidden" } : undefined}
      className={[style.wrapper, className].join(" ")}
    >
      <div className={style.headerLine} onClick={clickOpen}>
        <span>{cardTitle}</span>
        <FontAwesomeIcon icon={open ? faChevronUp : faChevronDown} />
      </div>
      <div
        className={[open ? style.show : style.hide, innerClassName].join(" ")}
      >
        {children}
      </div>
    </div>
  );
}
