import React from "react";
import { OptionsType } from "react-select/src/types";
import { ActorOption, Choice, Script, ScriptOption } from "../../../../types";
import Selector, {
  handleSelectorSave,
} from "../../../util/line-elements/selector";
import LabeledTextInput from "../../../util/labeled-text-input";
import Button from "../../../util/button";
import style from "./style.module.scss";

export interface ChoiceOptionProps {
  choice: Choice;
  actors: string[];
  scripts: Script[];
  onChange: (choice: Choice) => void;
  onActorAdd: (value: string) => void;
  onChoiceDelete: (choice: Choice) => void;
}

export default function ChoiceOption(props: ChoiceOptionProps) {
  const { actors, choice, scripts, onChange, onActorAdd, onChoiceDelete } =
    props;

  const actorOptions: OptionsType<ActorOption> = actors.map((actor) => {
    return {
      value: actor,
      label: actor,
    };
  });
  const scriptOptions: OptionsType<ScriptOption> = scripts.map((script) => {
    return {
      label: script.name,
      value: script.id,
    };
  });
  function handleInputChange(
    key: string,
    value: string
  ) {
    const clone = { ...choice };
    if (key === "affection change") {
      clone.affectionChange = parseFloat(value);
      onChange(clone);
    }
    if (key === "text") {
      clone.text = value;
      onChange(clone);
    }
    return undefined;
  }

  function handleActorChange(value: ActorOption | null) {
    const clone = { ...choice };
    if (value) clone.affectionActor = value.value;
    else clone.affectionActor = "";
    onChange(clone);
  }

  function handleScriptChange(value: ScriptOption | null) {
    const clone = { ...choice };
    if (value) clone.jumpToScriptId = value.value;
    else clone.jumpToScriptId = "";
    onChange(clone);
  }
  function handleActorAdd(value: string) {
    return handleSelectorSave<string>(
      "Actor",
      value,
      actors,
      (item) => item === value,
      onActorAdd
    );
  }

  function handleDelete() {
    onChoiceDelete(choice);
  }

  return (
    <div className={style.container}>
      <div className={style.topBar}>
        <span>Choice Option</span>
        <Button color="red" onClick={handleDelete}>
          Delete Choice (choice yoo)
        </Button>
      </div>
      <LabeledTextInput
        entityName="text"
        value={choice.text}
        onChange={handleInputChange}
      />
      <Selector
        value={scriptOptions.find(
          (item) => item.value === choice.jumpToScriptId
        )}
        className={style.selector}
        options={scriptOptions}
        entityName="Jump To Script"
        onChange={handleScriptChange}
      />
      <LabeledTextInput
        type="number"
        entityName="affection change"
        value={choice.affectionChange.toString()}
        onChange={handleInputChange}
      />
      <Selector
        value={actorOptions.find(
          (item) => item.value === choice.affectionActor
        )}
        className={style.selector}
        addable
        options={actorOptions}
        entityName="Affection Actor"
        onChange={handleActorChange}
        onAddSave={handleActorAdd}
      />
    </div>
  );
}
