import { v4 as uuid } from "uuid";
import {
    BackgroundChangeElement,
    ChoiceElement,
    DialogueElement,
    EventElement,
    Script,
    ScriptChangeElement
} from "../types";

export function createScript(): Script {
    return {
        description: "",
        id: uuid(),
        lineElements: [],
        name: ""
    };
}

export function createDialogue(): DialogueElement {
  return {
      actorLeft: "",
      actorRight: "",
      id: uuid(),
      text: "",
      title: "",
      type: "Dialogue"
  };
}

export function createScriptChange(): ScriptChangeElement {
    return {
        actorLeft: "",
        actorRight: "",
        id: uuid(),
        scriptChangeToId: "",
        type: "ScriptChangeElement"
    };
}

export function createBackgroundChange(): BackgroundChangeElement {
    return {
        actorLeft: "",
        actorRight: "",
        background: "",
        id: uuid(),
        type: "Background"
    };
}
export function createChoiceElement(): ChoiceElement {
    return {
        actorLeft: "",
        actorRight: "",
        choices: [],
        id: uuid(),
        text: "",
        type: 'Choice'
    };
}
export function createEvent(): EventElement {
    return {
        actorLeft: "",
        actorRight: "",
        event: "",
        id: uuid(),
        name: "",
        type: "Event"
    };
}
