import React from "react";
import style from "./style.module.scss";

export interface ErrorSpanProps {
  show?: boolean;
  className?: string;
  message?: string;
}

export default function ErrorSpan(props: ErrorSpanProps) {
  const { className, message, show } = props;

  if (!show) return null;

  return <span className={[style.error, className].join(" ")}>{message}</span>;
}
