import React, { useRef, useState } from "react";
import "./App.scss";
import { v4 as uuid } from "uuid";
import Layout from "./components/layout";
import ScriptSelector from "./components/scrip-selector";
import { LineElement, Script } from "./types";
import { ScriptCollection } from "./types/script-collection";
import ScriptOverview from "./components/script-overview";
import ScriptCreator from "./components/script-creator";
import {
  createBackgroundChange,
  createChoiceElement,
  createDialogue,
  createEvent,
  createScriptChange,
} from "./lib/element-creator";
import { LineElements } from "./components/line-elements/line-element";

const scriptCollectionConst: ScriptCollection = {
  scripts: [],
  config: {
    actors: [],
    backgrounds: [],
  },
};
export default function App() {
  const [selectedScript, setSelectedScript] = useState<Script | null>(null);
  const [scriptCollection, setScriptCollection] = useState<ScriptCollection>(
    scriptCollectionConst
  );
  const aRef = useRef<HTMLAnchorElement>(null);
  function onCreateNewScript() {
    const clone = { ...scriptCollection };
    const newScript: Script = {
      description: "New Script",
      id: uuid(),
      lineElements: [],
      name: "New Script",
    };
    clone.scripts.push(newScript);
    setScriptCollection(clone);
  }

  function handleScriptCLicked(id: string) {
    const script = scriptCollection.scripts.find((item) => {
      return item.id === id;
    });
    setSelectedScript(script ?? null);
  }

  function onScriptChange(script: Script) {
    const clone = { ...scriptCollection };
    const scriptIndex = clone.scripts.findIndex(
      (item) => item.id === script.id
    );
    if (scriptIndex !== -1) {
      clone.scripts[scriptIndex] = script;
    }
    setScriptCollection(clone);
    setSelectedScript(clone.scripts[scriptIndex]);
  }
  function handleElementCreate(
    elementName:
      | "dialogue"
      | "choice"
      | "background-change"
      | "script-change"
      | "event"
  ) {
    if (!selectedScript) {
      return;
    }
    const scriptCollectionClone = { ...scriptCollection };
    const scriptIndex = scriptCollectionClone.scripts.findIndex((item) => {
      return item.id === selectedScript.id;
    });
    const script = scriptCollectionClone.scripts[scriptIndex];
    if (!script) {
      return;
    }
    let lineElement: LineElement;

    switch (elementName) {
      case "dialogue":
        lineElement = createDialogue();
        break;
      case "choice":
        lineElement = createChoiceElement();
        break;
      case "background-change":
        lineElement = createBackgroundChange();
        break;
      case "script-change":
        lineElement = createScriptChange();
        break;
      case "event":
        lineElement = createEvent();
        break;
      default:
        throw Error("Button didn't return right string!");
    }
    script.lineElements.push(lineElement);
    setScriptCollection(scriptCollectionClone);
  }

  function handleLineElementChange(lineElements: LineElement[]) {
    if (!selectedScript) {
      return;
    }
    const scriptCollectionClone = { ...scriptCollection };
    const scriptIndex = scriptCollectionClone.scripts.findIndex((item) => {
      return item.id === selectedScript.id;
    });
    const script = scriptCollectionClone.scripts[scriptIndex];
    if (!script) {
      return;
    }
    script.lineElements = lineElements;
    setScriptCollection(scriptCollectionClone);
  }
  function handleActorAdd(value: string) {
    const clone = { ...scriptCollection };
    clone.config.actors.push(value);
    setScriptCollection(clone);
  }

  function handleBackgroundAdd(value: string) {
    const clone = { ...scriptCollection };
    clone.config.backgrounds.push(value);
    setScriptCollection(clone);
  }

  function onUpload(file: string) {
    setScriptCollection(JSON.parse(file));
  }

  function onSave() {
    if (aRef.current) {
      const blob = new Blob([JSON.stringify(scriptCollection,null,2)]);
      aRef.current.href = URL.createObjectURL(blob);
      aRef.current.click();
      aRef.current.href = "/#";
    }
  }
  return (
    <Layout onSaveClick={onSave} onUploadClick={onUpload}>
      <ScriptSelector
        onScriptClicked={handleScriptCLicked}
        onCreateNewScript={onCreateNewScript}
        scripts={scriptCollection.scripts}
      />
      {selectedScript ? (
        <ScriptOverview script={selectedScript} onChange={onScriptChange} />
      ) : undefined}
      {selectedScript ? (
        <LineElements
          actors={scriptCollection.config.actors}
          backgrounds={scriptCollection.config.backgrounds}
          scripts={scriptCollection.scripts}
          lineElements={selectedScript.lineElements}
          onActorAdd={handleActorAdd}
          onBackgroundAdd={handleBackgroundAdd}
          onChange={handleLineElementChange}
        />
      ) : undefined}
      <ScriptCreator onCreateElement={handleElementCreate} />
      <a
        style={{ display: "none" }}
        download="script.json"
        href="/#"
        ref={aRef}
      >
        download it
      </a>
    </Layout>
  );
}
