import React, {ChangeEvent, useRef} from "react";
import style from "./style.module.scss";
import Button from "../util/button";

export interface HeaderProps {
  onUploadClicked?: (file: string) => void;
  onSaveClicked?: () => void;
}

export default function Header(props: HeaderProps) {
  const { onSaveClicked, onUploadClicked } = props;

  const ref = useRef<HTMLInputElement>(null);

  function handleUploadClicked(){
    if(ref.current){
      ref.current.click();
    }
  }

  function handleFileChanged(e: ChangeEvent<HTMLInputElement>){
    if(!e.target.files) return;
    if(e.target.files.length > 0) {
      const file = e.target.files.item(0);
      if(file){
        file.text().then((item)=>{
          if (onUploadClicked) {
            onUploadClicked(item);
          }
        })
      }
    }
  }

  return (
    <header className={style.header}>
      <div>
        <div className={style.headerText}>
          <span>BackwashAcademyScriptWriter</span>
        </div>
        <div className={style.buttonsWrapper}>
          <Button onClick={handleUploadClicked}>
            <span>Upload File</span>
          </Button>
          <Button onClick={onSaveClicked}  color="green">
            Save File
          </Button>
          <input ref={ref} type='file' onChange={handleFileChanged}/>
        </div>
      </div>
    </header>
  );
}
