import React, {ReactNode} from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronUp, faChevronDown,faTimes } from "@fortawesome/free-solid-svg-icons";
import Button from "../../util/button";
import style from './style.module.scss';

export interface LineElementBodyProps {
    children?: ReactNode;
    onUp: () => void;
    onDown: () => void;
    onDelete: () => void;
}

export default function LineElementBody(props: LineElementBodyProps) {

    const {children, onDelete, onDown, onUp} = props;

    return (
        <div className={style.wrapper}>
            <div className={style.buttons}>

                <div className={style.deleteButton}>
                    <Button color='red' onClick={onDelete}><FontAwesomeIcon icon={faTimes}/></Button>
                </div>
                <div className={style.moveButtons}>
                    <Button onClick={onUp}><FontAwesomeIcon icon={faChevronUp}/></Button>
                    <Button onClick={onDown}><FontAwesomeIcon icon={faChevronDown}/></Button>
                </div>
            </div>
            <div>
                {children}
            </div>
        </div>
    )
}
