import React from "react";
import style from "./style.module.scss";
import TextInput from "../../text-input";
import ErrorSpan from "../../error-span";
import Button from "../../button";
import Popup from "../../popup";

export interface CreatePopupProps{
    show: boolean;
    inputValue: string;
    entityName: string;
    onInputChanged: (value: string) => void;
    showError?: boolean;
    errorMessage?: string;
    onSave: () => void;
    onClosePopup: () => void;
}

export default function CreatePopup(props: CreatePopupProps){

    const {errorMessage, onSave, inputValue, onClosePopup, onInputChanged, show, showError,entityName} = props;

    return (
        <Popup show={show}>
            <div className={style.popupContainer}>
                <span className={style.popupHeadline}>Add {entityName}</span>
                <div className={style.popupInputWrapper}>
                    <span>{entityName} Name: </span>
                    <TextInput
                        value={inputValue}
                        onChange={onInputChanged}
                    />
                </div>
                <ErrorSpan show={showError} message={errorMessage} />
                <div className={style.buttonRow}>
                    <Button color="green" onClick={onSave}>
                        Save
                    </Button>
                    <Button onClick={onClosePopup}>Cancel</Button>
                </div>
            </div>
        </Popup>
    )
}
