import React, {useEffect, useRef, useState} from "react";
import { Script } from "../../types";
import Button from "../util/button";
import style from "./style.module.scss";

export interface ScriptSelectorProps {
  scripts: Script[];
  onCreateNewScript: () => void;
  onScriptClicked:( id :string ) => void;
}

export default function ScriptSelector(props: ScriptSelectorProps) {

    const scriptsShownAtOnce = 3;

    const { scripts, onCreateNewScript, onScriptClicked } = props;

    const [offset,setOffset] = useState<number>(0);
    const [boxsize,setBoxSize] = useState(0);
    const selectorRef = useRef<HTMLDivElement>(null);

    function transformSelector(multi:number){
        if(selectorRef.current){
            selectorRef.current.setAttribute("style",`transform:translateX(${(boxsize+((12.5/3)*scriptsShownAtOnce)) * offset * multi}px);`)
        }
    }
    function onNextPage(){
        if(offset + scriptsShownAtOnce + 1 > scripts.length){
            setOffset(0);
        }
        else setOffset(offset + 1);
    }
    function onPreviousPage(){
        if(offset - 1 <= 0){
            setOffset(scripts.length - scriptsShownAtOnce);
        }
        else setOffset(offset - 1);
    }

    function onResize(){
        if(selectorRef.current) {
            const viewSize = selectorRef.current.offsetWidth;
            const size = (viewSize/scriptsShownAtOnce)-((10.5/3)*scriptsShownAtOnce);
            for (const child of Array.from(selectorRef.current.children)) {
                child.setAttribute("style",`max-width:${size}px;min-width:${size}px`);
            }
            setBoxSize(size);
        }
        transformSelector(-1);
    }
    useEffect(()=>{
        onResize();
        window.onresize = onResize;
    },[selectorRef])

    useEffect(()=>{
        transformSelector(-1);
    },[offset])

  return (
    <div className={style.scriptSelector}>
      <div className={style.scriptSelectorHeader}>
          <span>Select Script</span>
        <Button onClick={onCreateNewScript}>
          <span>Create New Script</span>
        </Button>
      </div>
        <div className={style.scriptSelectorScriptsWrapper}>
            <div onClick={onPreviousPage} role="button" tabIndex={0} className={style.scriptSelectorButton}>
                <span>&lt;</span>
            </div>
            <div className={style.selectorView}>
                <div ref={selectorRef}>
                    {scripts.map((script) => {
                        return (
                            <div key={script.id} className={style.scriptSelectorScript} onClick={()=>{onScriptClicked(script.id)}}>
                                <div className={style.scriptSelectorProperty}>
                                    <span>ID:</span>
                                    <span>{script.id}</span>
                                </div>
                                <div className={style.scriptSelectorProperty}>
                                    <span>Name:</span>
                                    <span>{script.name}</span>
                                </div>
                                <div className={style.scriptSelectorProperty}>
                                    <span>Description:</span>
                                    <span>{script.description}</span>
                                </div>
                            </div>
                        );
                    })}
                </div>
            </div>
            <div onClick={onNextPage} role="button" tabIndex={0} className={style.scriptSelectorButton}>
                <span>&gt;</span>
            </div>
        </div>

    </div>
  );
}
